:root {
	--nav-size: 60px;
	--border: 1px solid red;
	--border-radius: 8px;
	--speed: 500ms;
	--text-color: #dadce1;
	--bg: rgb(0, 0, 0);
	--bg-accent: #242526;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

a {
	color: var(--text-color);
	text-decoration: none;
}

body {
	margin: 0;
}
